
.sideMenu {
  margin-top: 30px;
}
.sideMenu li {
  list-style-type: none;
  padding: 15px 10px;
  background: #000;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
}

.overlay-panel {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background: rgba(0,0,0,0.9)
}
.overlay-panel .brand {
  margin: 50px 0 0;
  display: inline-block;
}
.overlay-panel-title {
  margin: 50px 0 0;
}
.menu {
  text-align: center;
  list-style-type: none;
  margin: 50px 0 0;
}
.menu li {
  padding: 15px 5px;
  margin: 10px 0;
  font-size: 30px;
  text-transform: uppercase;
  color: #ff0;
  cursor: pointer;
  background: rgba(0,0,0,0.8);
  opacity: 0.7;
}
.menu li:hover {
  opacity: 1;
}
.start-card {
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 20px;
  border-radius: 10px;
}
.start-card h2 {
  font-size: 30px;
  margin-bottom: 20px;
}
.player-info {
  margin-bottom: 30px;
}
.player-info .form-control-wrap {
  width: 50%    ;
  margin: 0 10px;
}

.grids {
  background: #7367F0;
  margin: 0 auto;
  display: inline-block;
  font-size: 0;
  border-radius: 10px;
  /*box-shadow: 0 0 10px 0 #000;*/
  border: 2px solid #000;
}
.cell {
  display: inline-block;
  width: 50px;
  height: 50px;
  margin: 5px;
  background: #000;
  -webkit-transition: background 0.4s ease;
  transition: background 0.4s ease;
}

.players {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.players .v-s {
  border-radius: 50%;
  position: absolute;
  left: 48%;
  top: 10px;
  font-size: 20px;
  width: 50px;
  text-align: center;
  line-height: 48px;
  height: 50px;
  background: #ff0;
}

.player {
  padding: 15px;
  background: #fff;
  display: inline-block;
  margin: 0 5px;
  font-size: 20px;
  text-align: center;
}
.playerName {
  padding: 20px 30px;
  background: #fff;
}

.winner-card {
  position: absolute;
  border-radius: 10px;
  padding: 20px;
  width: 50%;
  z-index: 10;
  background: #fff;
  text-align: center;
  color: #000;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.winner-card h1 {
  font-size: 30px;
  color: #f00;
  margin: 20px 0 5px;
  text-transform: uppercase;
}
.winner-card h2 {
  font-size: 30px;
  margin: 10px 0 20px;
}
.winner-card .p-text{
  font-size: 16px;
}
.winner-icon {
  font-size: 120px;
  color: #ffff00;
}
.winner {
  font-weight: 700;
}
.tag-line {
  font-weight: 600;
  color: #f00;
  margin-bottom: 20px;
}


/** reset css **/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
